<template>
  <div id = "chirps-panel">
    <div id="chirps-title">
      Chirps
    </div>
    <form>
      <input class="form-control" id="chirps-ctrl" placeholder="Start typing" autocomplete="off" v-model="userinput" />
      <div id="cb-type">
        <input type="checkbox" id="feed-type" name="feed-type" v-model="followersMode"/>
        <label for="feed-type">Followers</label>
      </div>
    </form>
    <div>
      <ChirpItem
        v-show="!followersMode"
        v-for="chirp in chirps"
        :key="chirp.id"
        :chirp="chirp"
      />

      <ChirpItem
        v-show="followersMode"
        v-for="chirp in followedChirps"
        :key="chirp.id"
        :chirp="chirp"
      />
    </div>
  </div>
</template>

<script>
import { getCurrentInstance } from 'vue';
import _debounce from "lodash/debounce";
import ChirpItem from "@/components/ChirpItem.vue"

export default {
  name: "Chirps",
  components: {
    ChirpItem,
  },

  data() {
    return {
      chirps: [],
      followedChirps: [],
      followersMode: false,
      userinput: '',
      regularFeed: undefined,
      followedFeed: undefined,
    }
  },

  mounted() {
     const internalInstance = getCurrentInstance();
     const emitter = internalInstance.appContext.config.globalProperties.emitter
     
     emitter.on("new-chirp", newMessage => {
       newMessage.id = this.chirps.reduce((a, b) => Math.max(a, b)) + 1;
       this.chirps.unshift(newMessage);

    });

    this.regularFeed = new WebSocket("ws://localhost:8000/api/wsfullfeed");
    this.followedFeed = new WebSocket("ws://localhost:8000/api/wsfollowedfeed");

    this.regularFeed.onopen = () => {
      this.regularFeed.send("");
    };

    this.followedFeed.onopen = () => {
      this.followedFeed.send("");
    };

    this.regularFeed.onmessage = (event) => {
      this.chirps = [];
      let data = JSON.parse(event.data);
      for (let i = 0; i < data.length; i++) {
        this.chirps.push(data[i]);
      }
    };

    this.followedFeed.onmessage = (event) => {
      this.followedChirps = [];
      let data = JSON.parse(event.data);
      for (let i = 0; i < data.length; i++) {
        this.followedChirps.push(data[i]);
      }
    };
  },

  unmounted() {
    this.regularFeed.close();
    this.followedFeed.close();
  },
  
  watch: {
    userinput: _debounce(function(val) {
        this.regularFeed.send(val);
        this.followedFeed.send(val);
    }, 20),

    followersMode: function(val) {
      this.followersMode = val;
    }
  },
}
</script>

<style scoped>
#chirps-title {
  padding: 10px;
  font-size: 1.1rm;
  font-weight: bolder;
}

#chirps-ctrl {
  display: inline;
  width: 60%;
  padding: 15px;
  margin-bottom: 15px;
}

#cb-type {
  display: inline;
  margin-left: 15px;
  vertical-align: -12px;
}
</style>