<template>
  <div>
  <!-- <div style="display: flex"> -->
    <td>{{ user.name }}</td>
    <td>
      <button type="button" class="btn-followed" v-on:click="follow">
          <template v-if="user.followed"> Unfollow </template>
          <template v-else> Follow </template>
      </button>
    </td>
  </div>
</template>

<script>
export default {
  name: "UserItem",

  props: ["userProp"],
  data() {
    return {
      user: {},
    };
  },

  mounted() {
    this.user = this.userProp;
  },

  methods: {
    follow: async function () {
      let api = this.user.followed ? 'http://localhost:8000/api/unfollow' : 'http://localhost:8000/api/follow';
      await fetch(api, {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        credentials: 'include',
        body: JSON.stringify({FollowedName: this.user.name})
      });

      this.user.followed = !this.user.followed;
    },
  },
};
</script>

<style scoped>
td {
  width: 220px;
}

.btn-followed {
  border: none;
  width: 100px;
}
</style>