<template>
  
  <div class="user-profile">
    <form class="send-chirp-panel" @submit.prevent="submit" :class="{ '--exceeded': newChirpCharacterCount > 140 }">
      <label for="newchirp"><strong>New Chirp</strong> ({{ newChirpCharacterCount }}/140)</label>
      <textarea id="newchirp" class="new_chirp" rows="4" v-model="data.messageText"/>  
      <button class="w-100 btn btn-lg btn-primary" type="submit" :class="{ '--disabled': newChirpCharacterCount > 140 }">Chirp!</button>
    </form>
  </div>  
  
</template>

<script>
import { reactive, computed, getCurrentInstance } from 'vue';
import {useStore} from "vuex";

export default {
  name: "ChirpSend",
  // inject: ['appEmitter'],
  
  setup() {
    const internalInstance = getCurrentInstance();
    const emitter = internalInstance.appContext.config.globalProperties.emitter
    console.log('1', emitter);
    
    const data = reactive({
      messageText: '',
    })
    const store = useStore();
        
    const newChirpCharacterCount = computed(() => data.messageText.length);

    const submit = async () => {
      const response = await fetch('http://localhost:8000/api/message', {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        credentials: 'include',
        body: JSON.stringify(data)
      });

      const content = await response.json();

      if (content.status != 401) {
        const newMessage = {
          username: store.state.User.name,
          message: data.messageText,
          dateCreated: new Date().toISOString()
        }

      await emitter.emit("new-chirp", newMessage);

      data.messageText = '';
      }
      }
      
    return {
      data,
      submit,
      newChirpCharacterCount
    }
  }
};
</script>

<style scoped>
.user-profile {
  padding: 20px 5%;
  max-height: 230px;
  min-width: 32%;
  }

  .send-chirp-panel {
    width: 400px;
  }

  .send-chirp-panel.--exceeded {
    color: red;
    border-color: red;
  }

  .send-chirp-panel button.--disabled {
    border: 1px solid #999999;
    background-color: #cccccc;
    color: #666666;
    pointer-events: none;
  } 

  .user-profile__chirp-wrapper {
    display: grid;
    grid-gap: 10px;
    margin-bottom: auto;
  }

  .user-profile__user-panel {
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: white;
    border-radius: 5px;
    border: 1px solid #DFE3E8;
    margin-bottom: auto;
  }

  .new_chirp{
    width: 100%; 
  }
  
</style>