<template>       
    <div id="main-message">
      {{ message }} 
    </div> 
    <div id="chirp-panel">
      <ChirpComponent id="send-bar"/>  
      <UsersComponent id="users-comp"/>   
      <Chirps id="chirps-comp"/>
    </div> 
</template>

<script>
import Chirps from "@/components/Chirps";
import ChirpComponent from "@/components/ChirpSend";
import UsersComponent from "@/components/Users";
import { onMounted, ref } from 'vue'
import {useRouter} from "vue-router";
import {useStore} from 'vuex'

export default {
  name: "Home",
  components: {
    ChirpComponent,
    UsersComponent,
    Chirps,
  },
  
  setup() {
    const message = ref('You are not logged in!');
    const store = useStore();
    const router = useRouter();

    onMounted(async () => {
      
        const response = await fetch('http://localhost:8000/api/user', {
          headers: {'Content-Type': 'application/json'},
          credentials: 'include',
        });
        
        const content = await response.json();
 
        if (content.status == 401)
        {
          await store.dispatch('Auth/setAuth', false);
          await router.push('/login');
        } else {
          message.value = `Hi, ${content.name}!`;
          await store.dispatch('Auth/setAuth', true);
          await store.dispatch('User/setName', content.name)
        }
      
      });
    return {
      message
    }
  }

  
}
</script>

<style>
#main-message{
  /* font-weight: bold; */
  font-size: 1.3rem;
}

#chirp-panel {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas:
    "send-bar chirps-comp chirps-comp"
    "users-comp chirps-comp chirps-comp"
    "users-comp chirps-comp chirps-comp"
    "users-comp chirps-comp chirps-comp";
  min-height: 100vh;
}
body {
  margin: 0;
}
#chirp-panel > * {
  border: 1px solid #f5f5f5;
}
#chirp-panel > *:hover {
  border: 1px solid #888;
  background-color: #f5f5f5;
}

#chirp-panel > #send-bar {
  grid-area: send-bar;
}

#chirp-panel > #chirps-comp {
  grid-area: chirps-comp;
}
#chirp-panel > #Users-comp {
  grid-area: users-comp;
}
</style>
