<template>
  <div id="users-panel">
    <div id="users-title">
      Users
    </div>
    <table class="table table-striped table-bordered">
      <tbody>     
        <tr>
          <UserItem v-for="user in users" :key="user.id" :userProp="user" />
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import UserItem from '@/components/UserItem.vue'

export default {
  name: 'Users',

  components: {
      UserItem,
  },

  data() {
    return {
      users: []
    }
  },

  async mounted() {
      let response = 
        await fetch('http://localhost:8000/api/followed', {
          headers: {'Content-Type': 'application/json'},
          credentials: 'include',
        });

      const followedObject = await response.json(); 
      const followedSet = new Set(followedObject.map(({ followed }) => [followed].toString())); 

      response = await fetch('http://localhost:8000/api/users', {
        headers: {'Content-Type': 'application/json'},
        credentials: 'include',
      });

      this.users = await response.json();
      for (var i = 0; i < this.users.length; i++) {
        let user = this.users[i];
        if (followedSet.has(user.name)) {
          user.followed = true;
        } else {
          user.followed = false;
        }
      }
  },
  methods: {
    follow : function(event) {
      console.log(event);
    }
  }
}
</script>

<style scoped>
#users-comp {
  position: absolute;
  top: 320px;
  min-width: 32.6%;
}
#users-title {
  padding: 10px;
  font-size: 1.1rm;
  font-weight: bolder;
}
</style>